<template>
  <v-container>
    <v-row
      justify="center"
      class="mt-16"
    >
      <v-col
        cols="4"
        md="6"
        class="mt-0"
      >
        <base-material-card
          icon="mdi-account-plus"
          class="px-5 py-3"
          title="用户登录/注册"
          color="primary"
        >
          <v-container class="pt-6">
            <v-row>
              <v-col cols="12">
                <v-text-field
                  v-model="userName"
                  label="用户名"
                  class="purple-input"
                  @keyup.enter="login"
                />
              </v-col>

              <v-col
                cols="12"
                class="text-right h1"
              >
                <v-btn
                  color="primary"
                  class="mr-0"
                  @click="login"
                >
                  确认
                </v-btn>
              </v-col>
            </v-row>
          </v-container>
        </base-material-card>
      </v-col>
    </v-row>
    <v-row>
      <base-material-snackbar
        v-model="snackbar"
        type="error"
        v-bind="{
          'top': true,
          'center': true
        }"
        :timeout="2000"
      >
        {{ errMessage }}
      </base-material-snackbar>
    </v-row>
  </v-container>
</template>

<script>
  import qs from 'qs'

  export default {
    data () {
      return {
        snackbar: false,
        errMessage: '',
        userName: '',
      }
    },
    methods: {
      login () {
        this.userName = this.userName.trim()
        if (this.userName.length === 0) {
          this.notify('用户名不能为空！')
          return
        }
        this.$axios.post(
          '/api/login',
          qs.stringify({ userName: this.userName }),
        ).then(res => {
          const qid = res.data.qid
          const questionNum = res.data.questionNum
          this.$store.commit('SET_LOGINED', true)
          this.$store.commit('SET_USER_NAME', this.userName)
          this.$store.commit('SET_QID', qid)
          this.$store.commit('SET_QUESTION_NUM', questionNum)
          this.$router.push({ path: '/instruction', query: {} })
        })
      },
      notify (message) {
        this.errMessage = message
        this.snackbar = true
      },
    },
  }
</script>
